<template>
  <div id="app">

    <md-app v-if="authenticated">
      <md-app-toolbar class="md-primary">
        <span class="md-title">Košuljice</span>
      </md-app-toolbar>

      <md-app-drawer md-permanent="full">
        <Drawer />
      </md-app-drawer>

      <md-app-content>
        <router-view/>
      </md-app-content>

    </md-app>

    <router-view v-else />

  </div>
</template>

<script>
import Drawer from '@/components/Drawer.vue'

export default {
  name: 'app',
  components: {
    Drawer,
  },

  data() {
    return {

    }
  },

  methods: {
    logout() {
      this.$store.commit('setUser', null);
      localStorage.clear();
      this.$router.push('/login');
    }
  },
  computed: {
    authenticated() {
      return this.$store.state.user;
    }
  },
  created() {
    const token = localStorage.getItem('token');
    if(!token) {
      this.$router.push('/login');
    } else {
      this.$store.commit('setUser', token);
    }


    if (this.$store.state.user == null) {
      this.$router.push("/login");
    }

  }
}
</script>

