<template>
  <div class="editing">
    <md-autocomplete v-model="editing.ime" :md-options="films" md-layout="box">
      <label>Ime Filma</label>
    </md-autocomplete>

    <md-button @click="backEdit">Cancel</md-button>
    <md-button @click="saveEdit">Save</md-button>
  </div>
</template>

<script>
export default {
  name: "Entry",
  data: () => ({
    editing: null,

    id: null,
    entry: null,

    films: ["Film 1", "Film 2", "Film 3", "Film 4", "Film 5", "Aaaa", "Ffff"]
  }),

  methods: {
    backEdit() {
      this.$router.go(-1);
    },

    saveEdit() {
      this.$store.commit("setEntry", {"id":this.id, "entry": this.entry, "editing": this.editing});
      this.backEdit();
    }
  },
  computed: {
    curr() {
      return this.$store.state.kos[this.id].content[this.entry];
    }
  },
  created() {
    this.id = this.$route.params.id;
    this.entry = this.$route.params.entry;
    this.editing = {...this.curr};
    this.editing.status = 2;
  }
};
</script>

