<template>
  <div class="kosuljica">
      <h2>{{curr.name}}</h2>
    <md-list>
      <md-list-item
      :to="'/kosuljica/' + id + '/' + film.id"
      class='entry'
        v-bind:key="film.ime"
        v-for="film in curr.content"
        v-bind:class="{active: film.status==0, pending: film.status==1, complete: film.status>=2}"
      >{{film.ime}}</md-list-item>
    </md-list>
    <md-button @click="backEntry">Back</md-button>
    <md-button @click="saveEntry">Save</md-button>
  </div>
</template>

<script>
export default {
  name: "Kosuljica",
  data: () => ({
    id: null
  }),
  methods: {
    backEntry() {
        this.$store.commit('setKosuljica', this.id);
      this.$router.go(-1);
    },

    saveEntry() {
      this.backEntry();
    }
  },
  computed: {
    curr() {
        return this.$store.state.kos[this.id];
    }
  },
  created() {
    this.id = this.$route.params.id;
  }
};
</script>

<style scoped lang='scss'>

.entry {
    margin: 5px;
}

.active {
  background: red;
}
.pending {
  background: yellow;
}
.complete {
  background: green;
}
</style>