import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: null,
    info: [],
    kos: [],
    editing: null,

  },
  mutations: {
    setUser(state, newUser) {
      state.user = newUser
    },

    setInfo(state, newInfo) {
      state.info = newInfo
    },

    setKos(state, newKos) {
      state.kos = newKos
    },

    setKosuljica(state, id) {
      var all = true;
      var one = false;
      for (var entry of state.kos[id].content) {
        if (entry.status == 0) {
          all = false;
        } else {
          one = true;
        }
      }
      if (all) {
        state.kos[id].status = 2;
      } else if (one) {
        state.kos[id].status = 1;
      }
    },

    setEntry(state, payload) {
      
      state.kos[payload.id].content[payload.entry] = payload.editing;
      state.kos[payload.id].content[payload.entry].status = 2;
    }


  },
  actions: {
  },
  modules: {
  }
})
