<template>
  <div class="centered-container">
    <md-content class="md-elevation-3">
      <div class="title">
        <div class="md-title">Login</div>
        <div class="md-body-1">Udruženje filmskih umetnika Srbije</div>
      </div>
      <form id="loginForm" v-on:submit="login($event)">
        <md-field>
          <label>Username</label>
          <md-input v-model="username" autofocus required></md-input>
        </md-field>
        <md-field md-has-password>
          <label>Password</label>
          <md-input v-model="password" type="password" required></md-input>
        </md-field>

        <div class="actions">
            <md-button type='submit' class='md-raised md-primary'>Login</md-button>
        </div>
      </form>
    </md-content>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: "",
      loading: false
    };
  },
  methods: {
    login(event) {
      if (document.getElementById("loginForm").checkValidity()) {
        document.getElementById("loginForm").checkValidity();
        localStorage.setItem("token", this.username);
        this.$store.commit("setUser", this.username);
        this.$router.push("/");
      } else {
        this.loading = false;
        document.getElementById("loginForm").reportValidity();
      }
      event.preventDefault();
    }
  },
  created() {
    const token = localStorage.getItem("token");
    if (!token) {
      this.$router.push("/login");
    } else {
      this.$store.commit("setUser", localStorage.getItem("token"));
      this.$router.push("/");
    }
  }
};
</script>

<style lang='scss'>
.centered-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100vh;
  .title {
    text-align: center;
    margin-bottom: 30px;
    img {
      margin-bottom: 16px;
      max-width: 80px;
    }
  }

  .md-content {
    z-index: 1;
    padding: 40px;
    width: 100%;
    max-width: 400px;
    position: relative;
  }
}
</style>